import React, { Component } from 'react';
import ReactDOM from 'react-dom';

import { MimirUI, Card } from 'mimir-ui';

class App extends Component {
    render() {
        return (
            <MimirUI>
                <Card>
                    <Card.Image src='download.jpeg' />
                    <Card.Content>
                        <Card.Header>
                            Gastroentology Reporting
                        </Card.Header>
                        <Card.Description>
                            Application for colonoscopy reportingh and disease diagnosis.
                        </Card.Description>
                    </Card.Content>
                </Card>
            </MimirUI>
        );
    }
}

const render = () => {
    ReactDOM.render(<App />,
        document.getElementById('root') || document.createElement('div'));
};

render();
